// 全局公共样式
#root {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  .ant-layout-content {
    padding: 20px 15px 0px 15px;
  }
}

// 滚动条样式
::-webkit-scrollbar {
  /*滚动条整体样式*/
  width: 6px; /*高宽分别对应横竖滚动条的尺寸*/
}
::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  border-radius: 10px;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.01);
  background: #eee;
}
::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  // width: 0;
}

// 全局h1
h1{
  color: red;
  font-size: 20px;
  text-align: center;
  border-bottom: 1px solid #eee;
}
h3{
  color: green;
  font-size: 16px;
  font-weight: bold;
}


// 布局
.flex{
  display: flex;
  &>div{
    flex: 1;
    padding: 10px;
    h1{
      background: gold;
      margin-bottom: 10px;
    }
  }
}

// 权限页面
.adminPage,
.editorPage,
.guestPage{
  display: flex;
  height: 300px !important;
  font-size: 24px;
  color: #fff;
  background-color: #409efe !important;
  border-radius: 12px !important;
  justify-content: center;
  align-items: center;
}

// 回到顶部
.ant-back-top{
  width: 30px;
  height: 30px;
  line-height: 30px;
  right: 15px;
  bottom: 20px;
  .anticon{
    line-height: 36px !important;
  }
}

// 折叠面包板
.ant-collapse{
  margin-bottom: 15px !important;
  background: #fff !important;
  .ant-collapse-header{
    margin-bottom: 5px;
    border-bottom: 1px solid #d9d9d9;
  }
}
@primary-color: #2858f8;